/* axios config */
import axios from "axios";
import store from './store'

// let apiUrl = "http://api.develop.eternity-backend.stag.stfalcon.com/api/v1.0";
let apiUrl = "http://api.master.eternity-backend.stag.stfalcon.com/api/v1.0";
// let apiUrl = 'https://api.memorial.eternity.co.il/api/v1.0';

// if(process.env.NODE_ENV === 'production') {
//   apiUrl = "http://produiction:3000";
// }

let commonConfig = {
    'Content-Type': 'application/json',
    'Accept': 'application/json',
    'Accept-Language': localStorage.getItem('vueml-lang'),
    // 'X-Requested-With': 'XMLHttpRequest'
    // 'Access-Control-Allow-Origin': '*'
};

let token = store.getters['auth/token'];

if(token) {
    commonConfig.Authorization = `Bearer ${store.getters['auth/token']}`;
}

// axios.defaults.withCredentials = true;
// axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*';
// window.axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';
//
axios.interceptors.request.use(
    async config => {

        config.headers = commonConfig;
        config.url = apiUrl+config.url;

        return config;
    },
    error => {
        Promise.reject(error)
    });

axios.interceptors.response.use((response) => {
    return response
}, async function (error) {
    const originalRequest = error.config;

    if (error.response.status === 401 && !originalRequest._retry) {
        originalRequest._retry = true;

        delete commonConfig['Authorization']

        await axios.post('/token/refresh', {
            refreshToken: store.getters['auth/refreshToken']
        }, { headers:  commonConfig })
            .then(async ({ data }) => {
                store.commit('auth/setToken', data);

                commonConfig['Authorization'] = `Bearer ${data.token}`;
                axios.defaults.headers.common['Authorization'] = `Bearer ${data.token}`;
            })
            .catch(error => {
                console.log(error);
            });

        //return axiosApiInstance(originalRequest);

        return originalRequest;
    }

    return Promise.reject(error);
});

window.axios = axios;

